	module.exports = {"data":{"post":{"id":"/home/runner/work/blog/blog/content/posts/2012-06-27--hamcrest-conflict-junit/index.md absPath of file >>> MarkdownRemark","html":"<p>Using jUnit 4.8 i got into an issue when testing the few <a href=\"http://code.google.com/p/lambdaj/\" title=\"LambdaJ\">lambda</a> expressions. I was constantly getting this error while testing code involving Hamcrest matchers.</p>\n<p>java.lang.NoSuchMethodError: org.hamcrest.core.AllOf.allOf(Lorg/hamcrest/Matcher;Lorg/hamcrest/Matcher;)Lorg/hamcrest/Matcher;</p>\n<p>However when i ran the application it was all fine. So i could sense the issue with the jUnit.</p>\n<p>The problem is due to hamcrest versioning issue with jUnit. jUnit uses an old version while other libraries (in my case LambdaJ) was using the latest version. The fix will be to download the junit-dep-4.*.jar from the jUnit <a href=\"https://github.com/KentBeck/junit/downloads\" title=\"gitHub kent beck\">download</a> page. Since you app already have Hamcrest class the test will run smoothly.</p>","htmlAst":{"type":"root","children":[{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Using jUnit 4.8 i got into an issue when testing the few "},{"type":"element","tagName":"a","properties":{"href":"http://code.google.com/p/lambdaj/","title":"LambdaJ"},"children":[{"type":"text","value":"lambda"}]},{"type":"text","value":" expressions. I was constantly getting this error while testing code involving Hamcrest matchers."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"java.lang.NoSuchMethodError: org.hamcrest.core.AllOf.allOf(Lorg/hamcrest/Matcher;Lorg/hamcrest/Matcher;)Lorg/hamcrest/Matcher;"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"However when i ran the application it was all fine. So i could sense the issue with the jUnit."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"The problem is due to hamcrest versioning issue with jUnit. jUnit uses an old version while other libraries (in my case LambdaJ) was using the latest version. The fix will be to download the junit-dep-4.*.jar from the jUnit "},{"type":"element","tagName":"a","properties":{"href":"https://github.com/KentBeck/junit/downloads","title":"gitHub kent beck"},"children":[{"type":"text","value":"download"}]},{"type":"text","value":" page. Since you app already have Hamcrest class the test will run smoothly."}]}],"data":{"quirksMode":false}},"fields":{"slug":"/hamcrest-conflict-junit/","prefix":"2012-06-27"},"frontmatter":{"title":"Hamcrest conflict in jUnit.","subTitle":"Hamcrest matchers are used in jUnit for assertions. There is a weird problem with the version conflict between Hamcrest and jUnit. The solution is been discussed here.","cover":null,"draft":null}},"author":{"id":"/home/runner/work/blog/blog/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Prasanna</strong> is a full stack web developer, with loads of experience in architecting and coding enterprise software solutions.  </p>"},"footnote":{"id":"/home/runner/work/blog/blog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>rants and writings through personal experience</li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":"670156599751120%"}}}},"pathContext":{"slug":"/hamcrest-conflict-junit/"}}

